import React,{ Component } from 'react';
import { Input, Form, Button, DatePicker,Select, Modal } from 'antd';
import { inject,observer } from 'mobx-react';
import { toJS } from 'mobx';
const { Option } = Select;

const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 12 },
};
const { moment } = window.GLOBAL_PARAMS;

@inject('DeviceDefineStore')
@observer
class AddModal extends Component{
  constructor(props){
    super(props)
    this.state = {
      value:'',
      radio:1,
      showMore : false
    }
    this.store = this.props.DeviceDefineStore;
  }
  render(){
    const { getFieldDecorator } = this.props.form;
    const { status } = this.props;
    const { configVisible,devCurrent,selectType,selectModel,employeeList,customerList} = toJS(this.store);
    return <Modal
      title={`${status === 'query' ? '查看' : status === 'add' ? "新增" : '配置'}`}
      visible={configVisible}
      onCancel={this.onClose}
      footer={
        status !== 'query' ? <div>
          <Button onClick={this.onClose}>取消</Button>
          <Button type='primary' onClick={this.onOk}>确定</Button>
        </div> : <Button type='primary' onClick={this.onClose}>关闭</Button>
      }
    >
      <Form>
        <Form.Item {...formItemLayout} label="设备类型">
          {getFieldDecorator('typeId', {
            initialValue: status !== 'add' ? devCurrent.typeId : undefined,
            rules: [{ required: true, message: '请选择设备类型' }]
          })(
            <Select 
              placeholder="请选择"
              style={{ width: 250 }} 
              disabled={status !== 'add'}
              onChange={this.onSelectType}
            > 
              {
                selectType.length > 0 && selectType.map(mas =><Option key={mas.id} value={mas.id}>{mas.typeName}</Option>)
              }
            </Select>
          )}
        </Form.Item>
        <Form.Item {...formItemLayout} label="设备型号">
          {getFieldDecorator('modelId', {
            initialValue: status !== 'add' ? devCurrent.modelId : undefined,
            rules: [{ required: true, message: '请选择设备型号' }]
          })(
            <Select 
              style={{width:250}}
              placeholder="请选择"
              disabled={status !== 'add'} 
            > 
              {
                selectModel.length > 0 && selectModel.map(mas =><Option key={mas.id} value={mas.id}>{mas.modelName}</Option>)
              }
            </Select>
          )}
        </Form.Item>
        <Form.Item {...formItemLayout} label="设备名称">
          {getFieldDecorator('deviceName', {
            initialValue:status !== 'add' ? devCurrent.deviceName : undefined,
            rules: [
              {
                required: true,
                message: '请填写设备名称',
              },
            ],
          })(
            <Input disabled={status !== 'add'} placeholder='请输入' style={{width:'250px'}} />
          )}
        </Form.Item>
        <Form.Item {...formItemLayout} label="设备编号">
          {getFieldDecorator('deviceCode', {
            initialValue:status !== 'add' ? devCurrent.deviceCode : undefined,
            rules: [
              {
                required: true,
                message: '请填写设备编号',
              },
            ],
          })(
            <Input disabled={status !== 'add'} placeholder='请输入' style={{width:'250px'}} />
          )}
        </Form.Item>
        
        <Form.Item {...formItemLayout} label="合作伙伴">
          {getFieldDecorator('customerId', {
            initialValue:status !== 'add' ? devCurrent.customerId : undefined,
            rules: [
              {
                required: true,
                message: '请选择合作伙伴',
              },
            ],
          })(
            <Select 
              placeholder="请选择"
              style={{ width: 250 }} 
            > 
              {
                customerList.length>0 && customerList.map(mas =><Option key={mas.id} value={mas.id}>{mas.customerName}</Option>)
              }
            </Select>
          )}
        </Form.Item>
        <Form.Item {...formItemLayout} label="安装位置">
          {getFieldDecorator('address', {
            initialValue:status !== 'add' ? devCurrent.address : undefined,
            
          })(
            <Input disabled={status === 'query'} placeholder='请输入' style={{width:'250px'}} />
          )}
        </Form.Item>
        <Form.Item {...formItemLayout} label="点检人员">
          {getFieldDecorator('checkId', {
            initialValue:status !== 'add' ? devCurrent.checkId : undefined,
          })(
            <Select 
              showSearch
              optionFilterProp='children'
              placeholder="请选择"
              style={{ width: 200 }} 
            > 
              {
                employeeList.length > 0 && employeeList.map(mas =><Option key={mas.id} value={mas.id}>{mas.name}</Option>)
              }
            </Select>
          )}
        </Form.Item>
        <Form.Item {...formItemLayout} label="巡检人员">
          {getFieldDecorator('inspectionId', {
            initialValue:status !== 'add' ? devCurrent.inspectionId : undefined,
          })(
            <Select 
              showSearch
              optionFilterProp='children'
              placeholder="请选择"
              style={{ width: 200 }} 
            > 
              {
                employeeList.length > 0 && employeeList.map(mas =><Option key={mas.id} value={mas.id}>{mas.name}</Option>)
              }
            </Select>
          )}
        </Form.Item>
        <Form.Item {...formItemLayout} label="维修人员">
          {getFieldDecorator('repairId', {
            initialValue:status !== 'add' ? devCurrent.repairId : undefined,
          })(
            <Select 
              showSearch
              optionFilterProp='children'
              placeholder="请选择"
              style={{ width: 200 }} 
            > 
              {
                employeeList.length > 0 && employeeList.map(mas =><Option key={mas.id} value={mas.id}>{mas.name}</Option>)
              }
            </Select>
          )}
        </Form.Item>
        <Form.Item {...formItemLayout} label="保养人员">
          {getFieldDecorator('maintainId', {
            initialValue:status !== 'add' ? devCurrent.maintainId : undefined,
          })(
            <Select 
              showSearch
              optionFilterProp='children'
              placeholder="请选择"
              style={{ width: 200 }} 
            > 
              {
                employeeList.length > 0 && employeeList.map(mas =><Option key={mas.id} value={mas.id}>{mas.name}</Option>)
              }
            </Select>
          )}
        </Form.Item>
      </Form>
    </Modal>
  }
  
  
  onOk = ()=>{
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let params = {
          ...values,
          // deviceTime:values.deviceTime.format('YYYY-MM-DD HH:MM:SS')
        }
        if(this.store.configStatus === 'edit'){
          params.id = this.store.devCurrent.id
        }
        this.store.saveDevice(params)
        this.onClose();
      }
    });
  }
  onSelectType = (value)=>{
    this.store.getSelectModel({typeId:value})
  }
  onClose=()=>{this.store.configVisible = false}
  componentDidMount(){
    this.store.getUsersByRoles()
    this.store.getCustomerList()
    this.store.getSelectType()
  }
  componentWillUnmount(){
    this.store.devCurrent = {}
    
  }
}
const WrappedDynamicRule = Form.create({})(AddModal);
export default WrappedDynamicRule;