import React from 'react'
import { inject, observer } from 'mobx-react'
// import { toJS } from 'mobx';
import SearchBar from './SearchBar'
import Table from './Table'
import AddModal from './AddModal'
import './index.less'
import { Tabs} from 'antd';

const { TabPane } = Tabs;
@inject('DeviceDefineStore')
@observer
class ConfigPage extends React.PureComponent {
  constructor(props) {
    super(props)
    this.store = this.props.DeviceDefineStore;
  }
  render() {
    return <div className='device_config tab-content-bg content-style'>
      <SearchBar />
      <div className="tabs-style">
        <Tabs activeKey={this.store.queryType.toString()} onChange={this.onStepChange} >
          <TabPane key={2} tab={'已配置'} className="tabpane-style"></TabPane>
          <TabPane key={1} tab={'未配置'} className="tabpane-style"></TabPane>
        </Tabs> 
      </div>
      
      <Table />
        {
          this.store.configVisible && <AddModal
            status={this.store.configStatus}
          />
        }  
    </div>;
  }
  onStepChange = (step) => {
    this.store.queryType = parseInt(step);
    this.store.getDevList();
  }
  componentDidMount() {
    this.store.queryType = 2;
  }
  componentWillUnmount() {
    this.store.devList = []
    this.store.devPage = {}
    this.store.queryType = 1;
    this.store.searchCondition = {};
    this.store.selectModel = []
    this.store.selectDevice = []
  }
}

export default ConfigPage