import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';
import TableLayout from 'components/TableLayout';
import { setDevConfigColumns } from '../cell/methods';
import { Modal } from 'antd';

@inject('DeviceDefineStore')
@observer
class OrderTable extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeviceDefineStore;
	}
	render() {
		return <div>{this.renderTable()}</div>;
	}
	renderTable = () => {
    const { devList,devPage,isDevLoading,queryType } = toJS(this.props.DeviceDefineStore);
		return (
			<TableLayout
				size="middle"
				style={{
					fontSize:'12px',
				}}
				rowKey='id'
				loading={isDevLoading}
				columns={setDevConfigColumns({
          onEdit:this.onEdit,onDelete:this.onDelete,queryType
        })}
				dataSource={devList}
				pagination={{
					...devPage,
					onChange: this.onPageChange
				}}
			/>
		);
	};
	onDelete = value =>{
		Modal.confirm({
			title: '提示',
			content: '确定删除当前信息？',
			okText: '确定',
			cancelText: '取消',
			onOk: () => {
				this.store.deleteDevice({
					id:value
				})
			}
		});
	}
  onEdit = (value,type)=>{
    this.store.configStatus = type;
		this.store.configVisible = true;
		this.store.getDevInfo({
			id:value
		}).then(()=>{
			this.store.getSelectModel({
				typeId:this.store.devCurrent.typeId
			})
		})
  }
	onPageChange = (page, pageSize) => {
		this.store.getDevList({
			pageSize,
			pageIndex: page,
			...this.store.searchCondition
		});
	};
	componentDidMount() {
		this.store.getDevList({queryType:2});
	}
}

export default OrderTable;
